.user-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-size: 1.5rem;
  color: white;
  width: 500px;
  max-width: 90vw;
  position: relative;
  font-family: initial;
  overflow-y: auto;
}

.profile-img-wrapper {
  width: 100px;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  background-color: transparent;
}

.img-and-input-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.img-uploader {
  color: white;
  display: flex;
  flex-direction: column;
  background-color: white;
  height: 32px;
  width: 32px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  margin-left: -1.5rem;
  border: 3px solid black;
}

.img-uploader input {
  font-size: 0;
  height: 100%;
  width: 100%;
  cursor: pointer;
  opacity: 0;
}

.img-uploader svg {
  height: 100%;
  width: 100%;
  padding: 4px;
  object-fit: contain;
  color: black;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: white;
  pointer-events: none;
}

.status {
  color: white;
  font-size: 1rem;
  max-width: 300px;
}

.user-card input,
.user-card textarea {
  background-color: transparent;
  color: white;
  font-size: 1.2rem;
  border-radius: 0.5rem;
  padding: 0.2rem 0.5rem;
  width: 100%;
  outline: none;
  border: 1.5px solid gray;
}

.user-card textarea {
  min-height: 150px;
  max-height: 40vh;
  resize: vertical;
  font-size: 1rem;
}

.user-card input:disabled,
.user-card textarea:disabled {
  display: flex;
  text-align: center;
  border: none;
  resize: none;
}

.technologies-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
}

.technologies-container div {
  display: flex;
  position: relative;
  height: 35px;
  aspect-ratio: 1/1;
}

.technologies-container img{
  border-radius: 8%;
}

.technologies-container button {
  padding: 0.1rem;
  background-color: #161617;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 50px;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
}
.technologies-container button svg {
  height: .7rem;
}

.technologies-container button:hover {
  background-color: #242526;
}

.technologies-search {
  width: 100%;
  position: relative;
}

.search-results {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: translate(0%, 105%);
  display: flex;
  flex-direction: column;
  max-height: 120px;
  width: 100%;
  background-color: inherit;
  overflow: auto;
  border: 1px solid gray;
  border-radius: 8px 8px 15px 15px;
  background-color: #131212;
  z-index: 10;
}

.search-result {
  display: flex;
  gap: 1rem;
  height: 40px;
  border-bottom: 1px solid gray;
  padding: 0.5rem;
  cursor: pointer;
}

.search-result div {
  height: 100%;
  display: flex;
  aspect-ratio: 1/1;
}

.search-result {
  font-size: 1.1rem;
}

.social-media-icon-wrapper {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-media-icon {
  fill: white;
  height: 50px;
  cursor: pointer;
}

.social-media-icon:hover:not(.social-media-icon-disabled) {
  scale: 1.2;
}

.social-media-icon-disabled {
  cursor: not-allowed;
  filter: blur(2px);
}

.buttons-wrapper {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.buttons-wrapper button {
  padding: 0.5rem 1rem;
  background-color: rgb(24, 23, 23);
  font-size: 1rem;
  border-radius: 5px;
  border: black;
  color: white;
  cursor: pointer;
}

.buttons-wrapper button:hover {
  background-color: rgb(36, 35, 35);
}
